html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video, button, textarea, a {
  background: transparent;
  background: none;
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  text-decoration: none;
  font-family: "Arial", "Helvetica";
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

blockquote, q {
  quotes: none;
}

blockquote {
&:before, &:after {
             content: '';
             content: none;
           }
}

q {
&:before, &:after {
             content: '';
             content: none;
           }
}

ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  padding: 0 0 1.5em 0;
}

input, select {
  vertical-align: middle;
}

/* End of Reset CSS */

body {
  background: #fff;
  width: 100%;
  height: 100%;
}

html {
  width: 100%;
  height: 100%;
}

.no-boxshadow {
input:invalid, textarea:invalid {}
}

/* By default the selection color is pink */

::-moz-selection, ::selection {
  background: #468ab1;
  color: #fff;
  text-shadow: none;
}

.ie7 img {
  -ms-interpolation-mode: bicubic;
}

ul, ol, p {
  margin-bottom: 15px;
}

ul, ol, ul li {
  list-style: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;

li {
  font-family: 'OpenSansRegular';
  font-size: 15px;
  line-height: 1.5;
}
}

p {
  line-height: 1.8;
  font-size: 13px;
}

a {
  cursor: pointer;

p & {
  font-family: 'OpenSansRegular';
  color: #4e4848;
}
}

article {
blockquote {
  border-left: 1px solid #ebe0b5;
  font-family: Arial;
  color: #ebe0b5;
  font-size: 13px;
  line-height: 22px;
  padding: 15px 0 15px 30px;
  margin: 5px 0 10px 30px;
}

ul {
  margin-left: 20px;

li {
  padding: 5px;
  list-style: square;
  font-family: Arial;
  color: #ebe0b5;
  font-size: 13px;
  line-height: 22px;
}
}

ol {
  margin-left: 20px;

li {
  padding: 5px;
  list-style-type: decimal;
  font-family: Arial;
  color: #ebe0b5;
  font-size: 13px;
  line-height: 22px;
}
}
}

/* remember to define focus styles! */

:focus {
  outline: 0;
}

img,
a img {
  height: auto;
  width: 100%;
  border: 0;
  display: block;
}

/* Float Definitions -----------------------------------------------------------*/

.clear {
  clear: both;
}

.right {
  float: right;
}

.left {
  float: left;
}

.alignright {
  float: right;
  margin: 0 0 2px 7px;
}

.alignleft {
  float: left;
  margin: 0 7px 2px 0;
}

img {
&.left, &.alignleft {
           float: left;
           margin: 0 15px 7px 0;
         }

&.right, &.alignright {
            float: right;
            margin: 0 0 7px 15px;
          }
}

.post img.aligncenter, .attachment img.aligncenter {
  display: block;
  margin: 0 auto;
  text-align: center;
}

.nopad {
  padding: 0px !important;
}

.nomargin {
  margin: 0px !important;
}

.noborder {
  border: none !important;
}

.mobile {
  display: none;

&.button {
   display: none;
 }
}

.col-width-33 {
  width: 33.33%;
}

.col-width-40 {
  width: 40%;
}

.col-width-50 {
  width: 50%;
}

.col-width-60 {
  width: 60%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  position: relative;
  line-height: 1;
}

h1 {
  font-family: 'PlayfairDisplayBold';
  margin-bottom: 25px;
  letter-spacing: 2px;
  font-size: 54px;
}

h2 {
  font-family: 'ProximaNovaBold';
  text-transform: uppercase;
  margin-bottom: 25px;
  letter-spacing: 2px;
  font-size: 16px;
  line-height: 1;
  letter-spacing: 2px;
}

h3 {
  font-family: 'PlayfairDisplayBold';
  letter-spacing: 2px;
  margin-bottom: 25px;
  font-size: 30px;
}

h4 {
  font-family: 'ProximaNovaBold';
  text-transform: uppercase;
  letter-spacing: 6px;
  margin-bottom: 25px;
  font-size: 24px;
  color: #96683e;
}

h5 {
  font-family: 'ProximaNovaSemibold';
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 25px;
  font-size: 16px;
}

h6 {
  font-family: 'OpenSansExtraBold';
  letter-spacing: 2px;
  margin-bottom: 25px;
  font-size: 24px;
  color: #96683e;
}

ol {
li {
  font-family: 'AvenirNextRegular';
  font-size: 12px;
  padding-bottom: 5px;
}
}

.visuallyhidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px; width: 1px;
  margin: -1px; padding: 0; border: 0;
}

a,
span {
  color: inherit;
  font: inherit;
}
